const cheerio = require("cheerio")

const addLinkAttrs = (data) => {
  const $ = cheerio.load(data)
  const links = $("a")
  // todo pass each callback as param
  links.each(function (i, link) {
    link.attribs.href =
      link.attribs.href + "?utm_source=ivos-ecotainment-newsletter"
    link.attribs.target = "_blank"
    link.attribs.rel = "noreferrer nofollow noopener"
  })
  return $.html()
}

module.exports = addLinkAttrs
